<template>
    <div id="app">
        <header>
            <div class="banner">
                <div class="banner-label">
                    <div class="banner-name">{{ config.blogName }}</div>
                    <div class="banner-tips">{{ config.tips }}</div>
                </div>
                <div class="banner-nav">
                    <template v-for="(v, i) in category">
                        <span v-if="v === currentCate" class="selected" v-on:click="goTo(v)">{{ v }}</span>
                        <span v-else v-on:click="goTo(v)">{{ v }}</span>
                    </template>
                </div>
            </div>
        </header>
        <router-view/>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'Vuex'

export default {
    name: 'app',
    data () {
        return {
            cate: []
        }
    },
    methods: {
        ...mapActions([
            'setCurrentCate'
        ]),
        goTo (cate) {
            this.$router.push({
                name: 'list',
                params: {category: cate}
            })
        }
    },
    computed: {
        ...mapGetters([
            'config',
            'currentCate',
            'category'
        ])
    },
    mounted: function () {
    }
}
</script>

<style>
body {
    margin: 0;
    background-color: #f7f7f7;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* text-align: center; */
    color: #2c3e50;
    /*margin-top: 60px;*/
}

header {
    box-sizing: border-box;
    /*padding: 90px 15px;*/
    width: 100%;
    height: 150px;
    color: #fff;
    /*text-align: center;*/
    background-color: #1abc9c;
    background-image: linear-gradient(120deg,#155799,#159957);
    border-color: #16a085;
    padding-top: 20px;
    padding-bottom: 20px;
}

.selected {
    border-bottom: 2px solid red;
}

.banner {
    margin: 0 auto;
    width: 70%;
    height: 100%;
    line-height: 50px;
    font-size: 18px;
    font-weight: bold;
}

.banner-nav {
    height: 50px;
    width: 100%;
    text-align: right;
}

.banner-nav span {
    margin-left: 10px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    height: 20px;
    line-height: 20px;
}

.banner-nav span:hover {
    color: #1abc9c;
}

.banner-label {
    width: 100%;
}

.banner-name {
    font-size: 40px;
    line-height: 40px;
    letter-spacing: 2px;
    height: 50px;
}

.banner-tips {
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 1px;
}

.content {
    width: 70%;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
}

</style>



